<template>
  <div id="legal-pay" :class="skins=='days'?'whites':'whites black'">
    <div class="title">
      <span>{{$t('fat.payPlease')}}</span>
      <span>￥{{msg.deal_money || '0.000' | numFilters(3)}}</span>
    </div>
    <div class="info">
      <div>
        <span>{{$t('fat.tdPrice')}}：</span>
        <span>{{msg.price || '0.000' | numFilters(3)}}</span>
      </div>
      <div>
        <span>{{$t('fat.tdNum')}}：</span>
        <span>{{msg.number || '0.000' | numFilters(3)}}{{msg.currency_name}}</span>
      </div>
      <div>
        <span>{{$t('fat.payinfo')}}</span>
        <span v-if="msg.way_name == 'ali_pay'">{{$t('fat.alipay')}}</span>
        <span v-else-if="msg.way_name == 'we_chat'">{{$t('fat.wechat')}}</span>
        <span v-else>{{$t('fat.bank')}}</span>
      </div>
      <div>
        <span>{{$t('fat.payAccount')}}：</span>
        <span>{{msg.hes_account}}</span>
      </div>
      <div>
        <span>{{$t('fat.realName')}}：</span>
        <span>{{msg.hes_realname}}</span>
      </div>
      <div>
        <span>{{$t('td.callWay')}}：</span>
        <span>{{msg.seller_phone}}</span>
      </div>
      <div>
        <span>{{$t('td.renum')}}：</span>
        <span>{{msg.id}}</span>
      </div>
      <div v-if="msg.bank_address">
        <span>{{$t('fat.shoperZname')}}：</span>
        <span>{{msg.bank_address}}</span>
      </div>
      <div>
        <span>{{$t('fat.shoperNum')}}：</span>
        <span>{{msg.seller_name}}</span>
       
      </div>
      <div class="pay-code" v-if="msg.way_name == 'ali_pay'&&msg.to_pay_info.alipay_qr_code|| msg.way_name == 'we_chat'&&msg.to_pay_info.wechat_qr_code">
        <span>{{$t('fat.QRcode')}}：</span>
        <img
          width="150"
          height="150"
          v-if="msg.way_name == 'ali_pay'"
          :src="msg.to_pay_info.alipay_qr_code"
          alt
        >
        <img
          width="150"
          height="150"
          v-if="msg.way_name == 'we_chat'"
          :src="msg.to_pay_info.wechat_qr_code"
          alt
        >
      </div>
      <div class="flex alcenter">
      <span>支付凭证</span>
      <div class="uploads">
          <img :src="src2" alt style="width:100px;height:100px;">
          <input type="file" id="file" accept="image/*" name="file" @change="file2">
      </div>
    </div>
      <div
        class="red bold letSpace"
        v-if="times"
      >{{$t('fat.please')}} {{times}} {{$t('fat.Introvert')}} {{msg.seller_name}} {{$t('fat.payment')}}</div>
      
      <div class="btns">
        <div class="btn" @click="showCancel = true">{{$t('fat.odCeil')}}</div>
        <div class="btn" @click="showConfirm = true">{{$t('fat.imPay')}}</div>
       
      </div>
     
    </div>
    
    <!-- 取消订单按钮 -->
    <el-dialog title :visible.sync="showCancel" width="300px" center>
      <div class="transfer-content">
        <h3>{{$t('fat.tdCeil')}}</h3>
        <div class="transfer-list">
          <p>{{$t('fat.pCeil')}}</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="cancel">{{$t('cuy.confirm')}}</el-button>
      </span>
    </el-dialog>
   
    <!-- 付款确认 -->
    <el-dialog title :visible.sync="showConfirm" width="300px" center>
      <div class="transfer-content">
        <h3>{{$t('fat.paySure')}}</h3>
        <div class="transfer-list">
          <p>{{$t('fat.pdopay')}}</p>
          <p>{{$t('fat.badClick')}}</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm">{{$t('cuy.confirm')}}</el-button>
      </span>
    </el-dialog>
   
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: "",
      token: "",
      showConfirm: false,
      showCancel: false,
      hasPay: false,
      id: "",
      src: "",
      times: "",
      set: null,
      skins: localStorage.getItem("skin") || "days",
      src2:'../../static/imgs/addimg.png'
    };
  },
  created() {
    var token = window.localStorage.getItem("token") || "";
    // this.id = this.$route.query.id;
    if (token) {
      this.token = token;
      this.id = this.$route.query.id;
      this.getData();
    }
  },
  methods: {
    getData() {
      var that = this;
      var i = layer.load();
      this.$http({
        url: "/api/legal_deal",
        params: {
          id: that.id
        },
        headers: { Authorization: that.token }
      }).then(res => {
        layer.close(i);
        if (res.data.type == "ok") {
  
          that.msg = res.data.message;
          var time5 = res.data.message.cancel_time;

          if (res.data.message.cancel_time) {
            if (time5 - 0 < 3600) {
              that.set = setInterval(function() {
                $(".time-order").show();
                time5--;
                var time1 = parseInt(time5 / 60);
                var time2 = time5 % 60;
                if (time1 > 0 && time2 > 0) {
                  that.times = time1 + that.$t('fat.minute') + time2 + that.$t('fat.second');
                } else if (time2 > 0) {
                  that.times = time2 + that.$t('fat.second');
                } else if (time1 == 0 && time2 == 0) {
                  that
                    .$http({
                      url: "/api/handle_one",
                      method: "post",
                      data: { id: that.id },
                      headers: { Authorization: that.token }
                    })
                    .then(res => {
                      window.history.go(-1);
                    })
                    .then(() => {});
                }
              }, 1000);
            } else {
              that.set = setInterval(function() {
                $(".time-order").show();
                time5--;
                var time1 = parseInt(time5 / 60 / 60);
                var time3 = parseInt(time5 / 60) - 60 * time1;
                var time2 = time5 % 60;
                if (time1 > 0 && time2 > 0 && time3 > 0) {
                  that.times = time1 +that.$t('fat.hour') + time3 + that.$t('fat.minute') + time2 + that.$t('fat.second');
                } else if (time2 > 0 && time3 > 0) {
                  that.times = time3 + that.$t('fat.minute') + time2 + that.$t('fat.second');
                } else if (time2 > 0) {
                  that.times = time2 + that.$t('fat.second');
                } else if (time1 == 0 && time2 == 0 && time3 == 0) {
                  that
                    .$http({
                      url: "/api/handle_one",
                      method: "post",
                      data: { id: that.id },
                      headers: { Authorization: that.token }
                    })
                    .then(res => {
                      window.history.go(-1);
                    })
                    .then(() => {});
                }
              }, 1000);
            }
          }
        }
      });
    },
    cancel() {
      var that = this;
      var srcs = '';
      if(that.src2 == '../../static/imgs/addimg.png'){
        srcs = '';
      }else{
        srcs = that.src2;
      }
      this.$http({
        url: "/api/user_legal_pay_cancel",
        method: "post",
        data: { id: this.id },
        headers: { Authorization: this.token }
      })
        .then(res => {
          layer.msg(res.data.message);
          if (res.data.type == "ok") {
            setTimeout(() => {
              this.$router.push("/legalRecord");
            }, 1000);
          }
        })
        .then(() => {
          this.showCancel = false;
        });
    },
    file2() {
      var that = this;
      var formData = new FormData();
      formData.append("file", $("#file")[0].files[0]);
      var i = layer.load();
      $.ajax({
        url: "/api/upload",
        type: "post",
        data: formData,
        processData: false,
        contentType: false,
        success: function(msg) {
          layer.close(i);
          if (msg.type == "ok") {
            that.src2 = msg.message;
            // if (that.src2) {
            //   that.showConfirm = true;
            // } else {
            //   layer.msg(that.$t("fat.imgUp"));
            // }
          }
        }
      });
    },
    confirm() {
      var that  = this;
      var srcs = '';
      if(that.src2 == '../../static/imgs/addimg.png'){
        srcs = '';
      }else{
        srcs = that.src2;
      }
      this.$http({
        url: "/api/user_legal_pay",
        method: "post",
        data: { id: this.id, pay_orders_img:srcs },
        headers: { Authorization: this.token }
      })
        .then(res => {
          layer.msg(res.data.message);
          if (res.data.type == "ok") {
            setTimeout(() => {
              this.$router.push("/legalRecord");
            }, 1000);
          }
        })
        .then(() => {
          this.showConfirm = false;
        });
    }
  },
  destroyed() {
    var that = this;
    if (that.set) {
      clearInterval(that.set);
    }
  }
};
</script>

<style lang='scss' scoped>
.uploads{
  position: relative;
  width:100px;
  height: 100px;

}
.uploads input{
  position: absolute;
  left: 0;
  top:0;
  width:100px;
  height: 100px;
  opacity: 0;

}
.whites {
  width: 1200px;
  margin: 66px auto 60px;
  background-color: #fff;
  padding-bottom: 30px;
  -webkit-box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  min-height: 630px;
  position: relative;
  top: 18px;
  > .title {
    margin-bottom: 20px;
    font-size: 20px;
    line-height: 60px;
    > span {
      padding: 0 30px;
    }
    > span:last-child {
      font-weight: 600;
    }
  }
  > .info {
    padding: 0 30px;
    line-height: 40px;
    > div {
      display: flex;
    }
    span:first-child {
      width: 140px;
    }
    > .btns {
      padding: 20px 0;
      cursor: pointer;
      > div {
        color: #fff;
        border-radius: 2px;
        padding: 0 16px;
        background: #f0b90b;
        margin-right: 30px;
        font-size: 14px;
      }
      > div:first-child {
        background: #ccc;
        color: #333;
      }
    }
  }
  > .cancel-box,
  > .confirm-box {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    > .content {
      margin: 200px auto 0;
      border-radius: 2px;
      width: 360px;
      background: #fff;
      line-height: 40px;
      text-align: center;
      > div:first-child {
        font-weight: 600;
      }
      > .flex {
        margin-top: 10px;
        border-top: 1px solid #cad7e0;
        cursor: pointer;
        div {
          width: 50%;
        }
        > div:first-child {
          border-right: 1px solid #cad7e0;
        }
      }
    }
  }
  .imgbtn {
    position: relative;
  }
  #file {
    position: absolute;
    top: 0;
    left: 0;
    width: 100px;
    height: 100px;
    opacity: 0;
    z-index: 99;
    cursor: pointer;
  }
  .pay-code span {
    line-height: 150px;
  }
}
.transfer-content h3 {
  text-align: center;
  color: #f0b90b;
  font-size: 18px;
}
.transfer-list {
  margin-top: 20px;
}
.transfer-list p {
  line-height: 30px;
  font-size: 14px;
}
.dialog-footer button {
  width: 150px;
  background-color: #f0b90b;
  border: 1px solid #f0b90b;
}
.el-button:focus,
.el-button:hover {
  background-color: #f0b90b;
  border: 1px solid #f0b90b;
  color: #fff;
}
.black {
  background-color: #1e2b34;
  color: #fff;
}
</style>



